import {useEffect, useState} from "react";
import React from "react"
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import {MenuItem, Button, Snackbar, IconButton} from '@mui/material';
import {Paper, Stack, Typography, CardContent, Card, TextField, FormGroup, Switch, InputLabel, Select} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

const Ideas = () => {
    const [ideaName, setIdeaName] = useState("");
    const [description, setDescription] = useState("");
    const [email, setEmail] = useState("");
    const [needs, setNeeds] = useState("");
    const [message, setMessage] = useState("");
    const [open, setOpen] = useState(false);

    console.log(ideaName);
    console.log(description);
    console.log(email);
    console.log(needs);

    
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
        setOpen(false);
      };
      const action = (
        <React.Fragment>
          <Button color="secondary" size="small" onClick={handleClose}>
            CLOSE
          </Button>
          <IconButton
            size="small"
            aria-label="close"
            color="inherit"
            onClick={handleClose}
          >
            <CloseIcon fontSize="small" />
          </IconButton>
        </React.Fragment>
      );


    let handleSubmit = async (e) => {
        try{  
                //uloží nápad do databaze
                let res = await fetch("http://localhost:3001/ideas/api/idea", {
                  method: "POST",
                  headers: { 'Content-Type': 'application/json' },
                  body: JSON.stringify({
                    ideaName: ideaName,
                    email: email,
                    description: description,
                    needs: needs,
                    approval: false
                  }),
                });
                let resJson = await res.json();
                if (res.status === 201) {
                  console.log("Nápad byl úspěšně zaevidován");
                  setMessage("Nápad byl úspěšně zaevidován");
                  setOpen(true);
                  setTimeout(()=>{
                    window.location.reload();
                  }, 750)
                }
              } catch (err) {
                console.log(err);
                setMessage("stala se nějaká chyba, zkontrolujte, že je tento email opravdu evidován ve spolkovém rejstříku");
                setOpen(true);
              }}
              return (<div>
                <Navbar></Navbar>
                <img  style={{width:'101%', padding: 0, margin: '-10px', position: 'absolute', top: 0, zIndex: -5}}></img>
                <Snackbar
                        open={open}
                        autoHideDuration={6000}
                        onClose={handleClose}
                        message= {message}
                        action={action}
                      />
                <Paper sx={{width:'70%', marginLeft:'auto', marginRight:'auto', marginTop: '15px', opacity: 0.9}}>
                <FormGroup><TextField id="standard-basic" 
                        label="Název akce/projektu" 
                        variant="standard" 
                        onChange={(e) => setIdeaName(e.target.value)}
                        sx={{width: '50%', marginLeft: '25%', marginTop:'25px'}}/><br></br>
                                             <Typography
                         sx={{width: '50%', marginLeft:'25%', marginTop: '50px'}}>
                        Stručný popis a vysvětlení, co za akci byste rádi navrhli, pro koho bude akce určená a co bude mít za význam.         </Typography>
                   <TextField id="standard-basic" 
                        label="Popis akce" 
                        variant="standard"
                        multiline 
                        onChange={(e) => setDescription(e.target.value)}
                        sx={{width: '50%', marginLeft:'25%'}}/><br></br>
                        
                   <Typography
                         sx={{width: '50%', marginLeft:'25%', marginTop: '40px', textAlign: 'justify'}}>
                        Co bude k uskutečnění daného nápadu potřeba? Bude třeba zajistit funding? Kde se bude akce pořádat?            </Typography>
                    <TextField id="standard-basic" 
                        label="Co bude na akci potřeba?" 
                        variant="standard"
                        multiline 
                        onChange={(e) => setNeeds(e.target.value)}
                        sx={{width: '50%', marginLeft:'25%'}}/><br></br>
                    <Typography
                    sx={{width: '50%', marginLeft:'25%', marginTop: '40px', textAlign: 'justify'}}>
                     Zde vložte svůj email, který je registrovaný jako člen spolku. Pokud nejste členem spolku: <a href="/Opportunities">Stát se členem</a> </Typography>
                    <TextField id="standard-basic" 
                        label="Email" 
                        variant="standard" 
                        onChange={(e) => setEmail(e.target.value)}
                        sx={{width: '50%', marginLeft:'25%'}}/><br></br>
                  <Button 
                  onClick={handleSubmit}
                  color='error' sx={{marginBottom:'50px', marginTop: '40px'}}>Poslat nápad</Button>
                    <Typography
                         sx={{width: '50%', marginLeft:'25%', textAlign: 'justify', marginTop: '5px', marginBottom: '25px'}}>
                         Váš návrh bude projednán spolkem a pokud bude rozhodnuto, že je nápad realizovatelný, určitě se Vám ozveme s dalšími informacemi a pokyny.           </Typography>
                   </FormGroup> </Paper>
                    </div>);
}
 



export default Ideas ;