import Navbar from './Navbar';
import React, {useState, useEffect} from 'react';
import {Button, Stack, Grid, TextField, AppBar, Card, Toolbar, ButtonGroup, FormGroup} from '@mui/material';
import { width } from '@mui/system';
import e from 'cors';
import dayjs from 'dayjs';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DateTimePicker  } from '@mui/x-date-pickers/DateTimePicker';

const isWeekend = (date) => {
  const day = date.day();

  return day === 0 || day === 6;
};


const Dashboard = () => {
document.body.style = 'background: #E0E0E0;';
const [caption, setCaption] = useState("");
const [contentShort, setContentShort] = useState("");
const [content, setContent] = useState("");
const [imageURL, setImageURL] = useState("");
const [message, setMessage] = useState("");
const [section, setSection] = useState("");
const [file, setFile] = useState(null);
const [value, setValue] = React.useState(dayjs('2023-01-01'));

//console.log(value);
const date = new Date(value["$d"]);
const isoDate = date.toISOString();

console.log(`ISO date: ${isoDate}`);
let handleSubmit = async (e,x) => {
try{  
        //uloží post do databáze
        const date = new Date(value["$d"]);
        const isoDate = date.toISOString();
        let res = await fetch(/*"https://prifuk.onrender.com/posts/api/post"*/"http://localhost:3001/posts/api/post", {
          method: "POST",
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            caption: caption,
            contentShort: contentShort, 
            content: content,
            date: isoDate,
            imageURL: file.name,
            section: x,
          }),
        });
        let resJson = await res.json();
        if (res.status === 201) {
          setCaption("");
          setContentShort("");
          setContent("");
          setImageURL("");
          setSection("")
          console.log("Příspěvek byl úspěšně zaevidován");
          const formData = new FormData();
          formData.append('image', file);
          console.log(file);
          fetch(/*'https://prifuk.onrender.com/posts/api/image'*/"http://localhost:3001/posts/api/image", {
              method: 'POST',
              body: formData
            })
            .then(response => response.json())
            .then(data => {
            })
            .catch(error => {
              console.error(error);
            });
            setTimeout(()=>{
              //window.location.reload();
            }, 250)
            alert("succesfull insertion")
                } else {
                    setMessage("Request has failed");
                  }
      } catch (err) {
        console.log(err);
      }}

return(<div style={{position: 'absolute', top: 0, width: '100%'}}>
<Navbar/>
<div style={{width:'60%', marginLeft:'auto', marginRight:'auto'}}>
    <h1>Přidat Event</h1><hr></hr>
    
    <TextField id="standard-basic" 
    label="Titulek" 
    variant="standard"
    onChange={(e) => setCaption(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '25%'}}
    /><br></br>
    <TextField id="standard-basic" 
    label="Krátký popis" 
    multiline
    variant="standard" 
    onChange={(e) => setContentShort(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <TextField id="standard-basic" 
    label="Text" 
    multiline variant="standard" 
    onChange={(e) => setContent(e.target.value)}
    sx={{width: '75%', marginLeft: '12.5%', marginRight: '12.5%', marginTop:'25px'}}/><br></br>
    <div style={{marginLeft: '45%', marginTop:'20px'}}><LocalizationProvider dateAdapter={AdapterDayjs} >
      <DateTimePicker
        renderInput={(props) => <TextField {...props} />}
        label="DateTimePicker"
        value={value}
        onChange={(newValue) => {
          setValue(newValue);//console.log(value);
        }}
      />
    </LocalizationProvider></div><br></br>
    <input type="file" style={{marginLeft: '45%', marginTop:'20px'}} onChange={(e) => setFile(e.target.files[0])}></input><br></br><br></br>
    <Button  onClick={(e) => handleSubmit(e,'events')}
    sx={{marginLeft: '40%', marginRight: '40%', marginTop:'25px'}} color="error">Vytvořit</Button>

</div>
</div>);
}
export default Dashboard;