import React, { useState, useEffect } from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import useFetch3 from '../Requester/useFetch3';
import {Button, ButtonGroup, Card, Input, Typography, Stack} from '@mui/material';
import responsiveDesign from './responsiveDesign.css';
import DrawerEl from './DrawerEl'

const Members = () => {
    const {data3, setData3} = useFetch3();
    const [nameHook, setNameHook] = useState("");
    const [surnameHook, setSurnameHook] = useState("");
    const [sectionHook, setSectionHook] = useState("");
    const [membershipHook, setMembershipHook] = useState("");
    console.log(data3);

    useEffect(() => {
        let m = "https://prifuk.onrender.com/members/api";
              setData3({ ...data3, slug: m });    
    setTimeout(()=>{
        }, 100)
      
      }, [])
    return ( 
       <div>         
    <div style={{width:'200px', marginLeft:'auto', marginRight:'auto'}}>
    <img style={{width:'200px'}} src={logo} id="logo"></img></div>
    <div id="navbar"> <Navbar></Navbar></div><div id="drawerElement"><DrawerEl></DrawerEl></div>
    <Typography sx={{textAlign: 'center', fontSize: '28pt'}} variant="h3">Veřejný spolkový rejstřík člennů</Typography>
    <div style={{width:'80%', paddingLeft:'25%'}}><Stack direction='row'>
        <Typography>Počet člennů: {data3.results.length}</Typography>
    </Stack></div>
<div id="memberFilter" style={{marginRight:'auto', marginLeft:'auto'}} >
    <Card sx={{marginLeft:'auto', marginRight:'auto', borderRadius: '16px', marginTop:'10px', marginBottom: "15px", padding: '10px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}} >
    <Input  placeholder="Jméno" onChange={event => setNameHook(event.target.value)} sx={{width:'25%', marginRight:'0px'}}></Input>
    <Input  placeholder="Příjmení" onChange={event => setSurnameHook(event.target.value)} sx={{width:'25%', marginRight:'0px'}}></Input>
    <Input  placeholder="Sekce" onChange={event => setSectionHook(event.target.value)} sx={{width:'25%', marginRight:'0px'}}></Input>
    <Input  placeholder="Člen" onChange={event => setMembershipHook(event.target.value)} sx={{width:'25%', marginRight:'0px'}}></Input>
    </Card>

    <Card sx={{width: '75%', marginLeft:'auto', marginRight:'auto', borderRadius: '16px', marginTop:'10px', marginBottom: "15px", padding: '10px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}} >
    <Input  placeholder="Jméno" onChange={event => setNameHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    <Input  placeholder="Příjmení" onChange={event => setSurnameHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    <Input  placeholder="Sekce" onChange={event => setSectionHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    <Input  placeholder="Člen" onChange={event => setMembershipHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    </Card>
  <div id="invisibleMobile">  {!data3.results ? "Loading..." : 
data3.results.slice(0).filter(function(q){
    return q.name.includes(nameHook)}).filter(function(a){
        return a.surname.includes(surnameHook)}).filter(function(s){
            return s.section.includes(sectionHook)}).filter(function(d){
                return d.membership.includes(membershipHook)}).map(({ _id, date, email, membership, musicTaste, name, publicationAuth, section, surname, telNumber}) =>
    <div style={{width: '75%', marginLeft:'150px', marginTop:'10px'}} key={date} >
    <Stack direction='row'>
    {publicationAuth ?<Typography sx={{width:'150px', marginRight:'50px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{name}</Typography>:<Typography sx={{width:'150px', marginRight:'50px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{"Anonym"}</Typography>}
    {publicationAuth ?<Typography sx={{width:'150px', marginRight:'50px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{surname}</Typography>:<Typography sx={{width:'150px', marginRight:'50px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{"Anonym"}</Typography>}  
    <Typography sx={{width:'150px', marginRight:'50px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{section}</Typography>
    <Typography sx={{width:'150px', marginRight:'50px', '@media(max-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{membership}</Typography></Stack>
    </div>
            )}</div>
<div id="invisibleWide">
{!data3.results ? "Loading..." : 
data3.results.slice(0).filter(function(q){
    return q.name.includes(nameHook)}).filter(function(a){
        return a.surname.includes(surnameHook)}).filter(function(s){
            return s.section.includes(sectionHook)}).filter(function(d){
                return d.membership.includes(membershipHook)}).map(({ _id, date, email, membership, musicTaste, name, publicationAuth, section, surname, telNumber}) =>
    <div style={{marginLeft:'15px', marginTop:'10px'}} key={date} >
    <Stack direction='row'>
    {publicationAuth ?<Typography sx={{width:'20%', marginRight:'15px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{name}</Typography>:<Typography sx={{width:'20%', marginRight:'15px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{"Anonym"}</Typography>}
    {publicationAuth ?<Typography sx={{width:'20%', marginRight:'15px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{surname}</Typography>:<Typography sx={{width:'20%', marginRight:'15px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{"Anonym"}</Typography>}  
    <Typography sx={{width:'20%', marginRight:'15px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{section}</Typography>
    <Typography sx={{width:'20%', marginRight:'15px', '@media(min-width: 480px)' : {visibility: 'hidden', display: 'none'}}}>{membership}</Typography></Stack>
    </div>
            )}
</div>
    
    </div>
    
    </div> );
}
 
export default Members;