import {React, useEffect} from 'react';
import Navbar from './Navbar'
import DrawerEl from './DrawerEl'
import logo from '../Media/logo.png'
import useFetch3 from '../Requester/useFetch3';
import Post from './EventPost';
import PostMobile from './EventPostMobile';
import backgroundPhoto from '../Media/Prifest/17.jpg'



let Events = () => {
    const {data3, setData3} = useFetch3();
    console.log(data3);

useEffect(() => {
        let m = "https://prifuk.onrender.com/posts/api";
              setData3({ ...data3, slug: m });    
    setTimeout(()=>{
        }, 100)
      
      }, [])
    
    return(
    
    <div style={{}}>
     
      <img src={backgroundPhoto} id="BackgroundOnlyWide" style={{width:'101%', padding: 0, margin: '-10px', zIndex:'-5', position: 'absolute', top: 0}}></img>
<div  id="navbar"><Navbar></Navbar></div>
<div id="drawerElement"><DrawerEl></DrawerEl></div>
<div id="eventsPosts" style={{marginLeft: 'auto', marginRight: 'auto', marginTop: '80px'}}>
{!data3.results ? "Loading..." : 
              data3.results.slice(0,6).map(({ _id, caption, contentShort, content, section, date, imageURL}) =>
          <div><div id="invisibleMobile"><Post caption={caption} contentShort={contentShort} content={content} imageURL={imageURL} key={date} _id={_id}></Post></div>
          <div id="invisibleWide"><PostMobile caption={caption} contentShort={contentShort} content={content} imageURL={imageURL} key={date} _id={_id}></PostMobile></div>
          </div>
            )}
</div></div>
)}

export default Events;