import React, { useState, useEffect } from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import {Paper, Stack, Typography, CardContent, Card, TextField, FormGroup, Switch, InputLabel, Select} from '@mui/material';
import {MenuItem, Button, Snackbar, IconButton} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import backgroundPhoto from '../Media/Prifest/12.jpg'
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import responsiveDesign from './responsiveDesign.css';
import DrawerEl from './DrawerEl'

const Join = () => {
    const [name, setName] = useState("");
    const [surname, setSurname] = useState("");
    const [email, setEmail] = useState("");
    const [telNumber, setTelNumber] = useState("");
    const [membership, setMembership] = useState("člen");
    const [publicationAuth, setPublicationAuth] = useState(true);
    const [yearValue, setYearValue] = useState(null);
    const [section, setSection] = useState("");
    const [startOfStudies, setStartOfStudies] = useState("");
    const [open, setOpen] = React.useState(false);
    const [message, setMessage] = useState("");
    
      const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
      };

      const action = (
        <React.Fragment>
          <Button color="secondary" size="small" onClick={handleClose}>
            CLOSE
          </Button>
          <IconButton
            size="small"
            aria-label="close"
            color="inherit"
            onClick={handleClose}
          >
            <CloseIcon fontSize="small" />
          </IconButton>
        </React.Fragment>
      );

    let handleSubmit = async (e) => {
        try{  
                //uloží člena do databaze
                let res = await fetch("https://prifuk.onrender.com/members/api/member", {
                  method: "POST",
                  headers: { 'Content-Type': 'application/json' },
                  body: JSON.stringify({
                    name: name,
                    surname: surname,
                    email: email,
                    telNumber: telNumber,
                    membership: membership,
                    publicationAuth: publicationAuth,
                    startOfStudies: startOfStudies,
                    section: section

                  }),
                });
                let resJson = await res.json();
                if (res.status === 201) {
                  console.log("Člen byl úspěšně zaevidován");
                  setMessage("Člen byl úspěšně zaevidován");
                  setOpen(true);
                  setTimeout(()=>{
                    window.location.reload();
                  }, 750)
                }
              } catch (err) {
                console.log(err);
                setMessage("Stala se nějaká chyba.G");
                setOpen(true);
              }}
    return (<div>
<div id="navbar"><Navbar></Navbar></div>
<div id="drawerElement"><DrawerEl></DrawerEl></div>
<img src={backgroundPhoto} id="BackgroundOnlyWide" style={{width:'101%', padding: 0, margin: '-10px', position: 'absolute', top: 0, zIndex: -5}}></img>
<Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        message= {message}
        action={action}
      />
<Paper sx={{width:'70%', marginLeft:'auto', marginRight:'auto', marginTop: '15px', opacity: 0.9, '@media(max-width: 480px)' : {
   width: "95%", marginTop: "60px"
  }}}>
<FormGroup><TextField id="standard-basic" 
        label="Jméno" 
        variant="standard" 
        onChange={(e) => setName(e.target.value)}
        sx={{width: '50%', marginLeft: '25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
    <TextField id="standard-basic" 
        label="Příjmení" 
        variant="standard" 
        onChange={(e) => setSurname(e.target.value)}
        sx={{width: '50%', marginLeft:'25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
    <TextField id="standard-basic" 
        label="Email" 
        variant="standard" 
        onChange={(e) => setEmail(e.target.value)}
        sx={{width: '50%', marginLeft:'25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
        <Typography
   sx={{width: '50%', marginLeft:'25%', marginTop: '60px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}
   >Rok začátku studia (tj. datum přijetí na Bc. studium): </Typography>
    <div style={{width: '50%', marginLeft:'auto',marginRight:'auto', marginTop: '25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "20%"}}}><LocalizationProvider dateAdapter={AdapterDayjs}>
        <DatePicker
          views={['year']}
          label="Year only"
          value={yearValue}
          onChange={(newValue) => {
            setStartOfStudies((new Date(newValue["$d"])).getFullYear()); setYearValue(newValue);
          }}
          renderInput={(params) => <TextField {...params} helperText={null} />}
        /></LocalizationProvider></div>
    <TextField id="standard-basic" 
        label="Telefonní číslo" 
        variant="standard" 
        onChange={(e) => setTelNumber(e.target.value)}
        sx={{width: '50%', marginLeft:'25%', marginTop:'25px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}/><br></br>
   <Typography
   sx={{width: '50%', marginLeft:'25%', marginTop: '60px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}
   >Dávám souhlas se zveřejněním svého jména, příjmení a sekce ve veřejném spolkovém rejstříku: </Typography>
   <Switch 
   onChange={(e) => setPublicationAuth(e.target.checked)}
   defaultChecked size="big" sx={{marginLeft:'46%', marginTop:'25px'}}/>
   <InputLabel
   sx={{width: '50%', marginLeft:'25%', marginTop:'25px'}}
   >Sekce</InputLabel>
  <Select
    labelId="demo-simple-select-label"
    id="demo-simple-select"
    sx={{width: '50%', marginLeft:'25%', marginTop:'10px', marginBottom: '50px', '@media(max-width: 480px)' : {width: "80%", marginLeft: "10%"}}}
    onChange={(e) => setSection(e.target.value)}
  >
    <MenuItem value={"Chemie"}>Chemie</MenuItem>
    <MenuItem value={"Geografie"}>Geografie</MenuItem>
    <MenuItem value={"Geologie"}>Geologie</MenuItem>
    <MenuItem value={"Biologie"}>Biologie</MenuItem>
  </Select>
  <Button 
  onClick={handleSubmit}
  color='error' sx={{marginBottom:'50px'}}>Přidat se</Button>
   </FormGroup> </Paper>
    </div>);
}
 
export default Join;