import * as React from 'react';
import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import responsiveDesign from './responsiveDesign.css';
import { deepOrange, lightGreen, pink, orange, purple} from '@mui/material/colors';

const drawerWidth = 240;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
  ({ theme, open }) => ({
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: `-${drawerWidth}px`,
    ...(open && {
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginLeft: 0,
    }),
  }),
);
const theme = createTheme({
    palette: {
      primary: {
        // Purple and green play nicely together.
        main: '#1e7c1f',
      },
      secondary: {
        // This is green.A700 as hex.
        main: '#1e7c1f',
      },
    },
  });
const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: 'flex-end',
}));

export default function PersistentDrawerLeft() {
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };
  const themeD = createTheme({
    palette: {
      primary: {
        // Purple and green play nicely together.
        main: '#1e7c1f',
      },
      secondary: {
        // This is green.A700 as hex.
        main: '#1e7c1f',
      },
    },
  });

let path = window.location.href;
const urlParts = path.split("/");
const pathway = urlParts[urlParts.length - 1];
  return ( 
  <ThemeProvider theme={themeD}>
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar position="fixed" open={open}  color="primary" >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ mr: 2, ...(open && { display: 'none' }) }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
          {pathway.includes("members")?<div>Veřejný rejstřík člennů</div>:<div></div>}
          {pathway.includes("about")?<div>O spolku</div>:<div></div>}
          {pathway.includes("events")?<div>Spolkové akce</div>:<div></div>}
          {pathway.includes("join")?<div>Přidat se ke spolku</div>:<div></div>}
          {pathway.includes("opportunities")?<div>Spolkové příležitosti</div>:<div></div>}
          {pathway.includes("rules")?<div>Spolkové pravidla a stanovy</div>:<div></div>}
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <DrawerHeader>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </DrawerHeader>
        <List>
        <Button href="/" variant="h6">
            Úvod
          </Button><br></br>
          <Button href="/events" variant="h6">
            Akce
          </Button><br></br>
          <Button  href="/about" variant="h6">
            O spolku
          </Button><br></br>
          <Button  href="/join" variant="h6">
            Přidat se
          </Button><br></br>
          <Button  href="/opportunities" variant="h6" >
           Příležitosti
          </Button><br></br>
          <Button  href="/members" variant="h6">
            Členové
          </Button><br></br>
          <Button  href="/rules" variant="h6">
            Pravidla a stanovy
          </Button>
        </List>
      </Drawer>
      <Main open={open}>
        <DrawerHeader />
       
      </Main>
    </Box></ThemeProvider>
  );
}