import {React, useEffect} from 'react';
import Navbar from './Navbar'
import {Paper, Stack, Typography, Button} from '@mui/material';
import logo from '../Media/logo.png'
import backgroundPhoto from '../Media/backgr.jpg'
import useFetch3 from '../Requester/useFetch3';
import Post from './EventPost';
import PostMobile from './EventPostMobile';
import responsiveDesign from './responsiveDesign.css';
import DrawerEl from './DrawerEl'

let Welcome = () => {
const {data3, setData3} = useFetch3();
useEffect(() => {
    let m = "https://prifuk.onrender.com/posts/api";
          setData3({ ...data3, slug: m });    
setTimeout(()=>{
    }, 100)
  
  }, [])

console.log(data3);
return(
<div style={{}}>
<img id="BackgroundOnlyWide" src={backgroundPhoto} style={{width:'101%', padding: 0, margin: '-10px', position: 'absolute', top: 0, zIndex: -5}}></img>
<div id="welcomeLogo" style={{position: 'relative', marginLeft: 'auto', marginRight: 'auto', zIndex: 2, backgroundColor: 'white', textAlign: 'center', borderRadius: '25px'}}>
<img src={logo}  style={{maxWidth:'93%', marginTop: '30px'}}></img>
</div>
<Button href="/events"  id="welcomeWhatsNew" variant="contained" sx={{position: 'relative',
 zIndex: 2, backgroundColor: 'green', textAlign: 'center',
 borderRadius: '25px', height: '60px', borderStyle: 'solid'}}>Co se zrovna děje?
</Button>
<div id="welcomeFeed" style={{marginTop: '30px', marginLeft: "auto", marginRight: 'auto'}}>
{!data3.results ? "Loading..." : 
              data3.results.filter(function(el){return el.section.includes("feed")}).slice(0,3).map(({ _id, caption, contentShort, content, section, date, imageURL}) =>
          <div><PostMobile caption={caption} contentShort={contentShort} content={content} imageURL={imageURL} key={date} _id={_id}></PostMobile></div>
          )}</div>


<div style={{position: 'absolute', top: '20px', left: 'auto', width: '100%'}} id="navbar"><Navbar></Navbar></div>
<div id="drawerElement"><DrawerEl></DrawerEl></div>
    
</div>
)}

export default Welcome;
//<Typography sx={{paddingTop:'15px'}} href="/events">Co je nového?</Typography>