import React from 'react';
import useImage from "../Requester/useImage";
import {Paper, Stack, Typography, CardContent, Card} from '@mui/material';

let EventPostMobile = ({caption, contentShort, content, imageURL, _id, section, className,...rest }) => {
    const { loading, error, image } = useImage(imageURL);
   // if (error) return <Typography>{"error"}</Typography>
    return(
    <div style={{marginBottom: '15px'}}>
        <Card  sx={{opacity:0.9}}>
        <CardContent>
            <Stack direction="row" spacing={2} sx={{width: '100%', borderRadius: '25px'}}>
                <div>
        <Stack direction="column" spacing={2} sx={{width: '100%'}}>
        <h3 style={{fontSize: '14pt', marginBottom: '-15px', marginTop: '-1px'}}>{caption}</h3>
        <p style={{fontSize: '12pt', textAlign:'justify'}}>{contentShort}</p>
        </Stack></div>
            </Stack>
        </CardContent>
      </Card></div>
)}

export default EventPostMobile;





