import {React, useEffect} from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import {Paper, Typography, Stack} from '@mui/material';
import { fontFamily } from '@mui/system';
import backgroundPhoto from '../Media/Prifest/69.jpg'
import pic1 from  '../Media/Prifest/13.jpg'
import pic2 from  '../Media/Prifest/26.jpg'
import pic3 from  '../Media/Prifest/46.jpg'
import pic4 from  '../Media/Prifest/61.jpg'
import DrawerEl from './DrawerEl'


const About = () => {
    return ( <div>
<div id="invisibleMobile" style={{width: "250px", marginLeft:'auto', paddingLeft: '25px', paddingRight: '25px', marginRight:'auto', backgroundColor:'white', paddingTop:'35px', borderRadius: '15px', opacity: 0.9}}>
<img style={{zIndex: '500', maxWidth: "200px", marginLeft: 'auto', marginRight:'auto'}} id="logo" src={logo}></img></div>
       <div id="navbar"> <Navbar></Navbar></div>
        <div id="drawerElement"><DrawerEl></DrawerEl></div>
        <img src={backgroundPhoto} id="BackgroundOnlyWide" style={{width:'101%', padding: 0, margin: '-10px', position: 'absolute', top: 0, zIndex: -5}}></img>
        <div id="invisibleMobile"><Paper sx={{marginLeft: 'auto', marginRight:'auto', marginTop: '20px', position: 'relative', width:'70%', fontFamily: "'Roboto Mono', monospace", opacity: 0.85, paddingBottom: '100px'}}>
            <Typography variant="h2" sx={{textAlign: 'center', fontFamily: "'Roboto Mono', monospace", paddingTop: '50px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}>O spolku Přífuk</Typography>
            
            <div style={{marginLeft: '50px', textAlign:'justify', paddingRight: '40px'}}>
                <Typography variant="h4" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop: "50px", '@media(max-width: 480px)' : {visibility: 'hidden'}}}>K čemu přífuk?</Typography>
                <Typography variant="p" sx={{fontFamily: "'Roboto Mono', monospace", marginLeft: '50px', textAlign:'justify', paddingRight: '20px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}>
             Spolek přífuk je studentskou institucí založenou na půdě Přírodovědecké fakulty Univerzity Karlovy. Je je ojedinělý projekt, který má
             za hlavní cíl zlepšovat studentský život v kampusu Albertov. Hlavní myšlenka spolku je prohlubování studentských vztahů a to nejen 
             v rámci sekce, ale i mezisekčně nebo i mezifakultně. Spolek byl, je a vždycky bude otevřen všem proaktivním studentům, kteří se chtějí
             přidat do akademické společnosti a přispět jakoukoliv kvalitou.
                 </Typography>
                 <Typography variant="h4" sx={{fontFamily: "'Roboto Mono', monospace", paddingTop:'40px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}>Jak toho dosáhnout?</Typography>
                 <Typography variant="p" sx={{fontFamily: "'Roboto Mono', monospace", marginLeft: '50px', textAlign:'justify', paddingRight: '20px', marginTop: '20px', '@media(max-width: 480px)' : {visibility: 'hidden'}}}>
                    Spolek není uzavřená společnost, ale je to společenství, které nabízí mnoho příležitostí pro všechny proaktivní studenty.
                    Důležitou myšlenkou spolku je vytvořit instituci, která zastřeší a zjednoduší organizaci jakýchkoliv projektů, se kterými přijde jakýkoliv 
                    člen spolku. Spolek si klade za cíl pomáhat tvořit nové věci a realizovat nápady všech, kteří projeví zájem o zlepšení zdejšího prostředí.
                    Stejně tak zakládá na spolupráci a ochotě pomoct všech všem se vším, s čím kdo přijde. Více v <a href="/Opportunities">Příležitostech</a>
                    </Typography></div>
        </Paper>
        <div style={{width: '80%', marginRight: 'auto', marginLeft: 'auto'}}>
        <Typography sx={{ fontFamily: "'Roboto Mono', monospace", marginTop: '100px', padding: '20px'}} variant="h5">Spolek organizuje nejlepší přírodovědecký studentský festival PříFest OpenAir</Typography>
        <Stack direction="row" spacing={2}>
            <div style={{ fontFamily: "'Roboto Mono', monospace", marginLeft: '25px', marginRight:'25px', marginBottom: '100px', textAlign:'justify', width: '300%'}}><Typography variant="p" sx={{ fontFamily: "'Roboto Mono', monospace", margin: '25px'}}>
                K příležitosti (z)bourání Albertovské menzy byl zorganizován oslavný festival právě v prostoru stoleté Albertovské menzy. Několik studentských 
                i nestudentských kapel vystoupilo na pódiu před menzou a vytvořily krásný hudební doprovod celé akce. Mimo to se akce účastnilo mnoho dalších 
                spřátelených studentských spolků, které se zhostily doprovodného programu. Na programu byl nealkoholický pivní pong, spolková prezentace nebo i
                osvěta o sexuálním násilí od nestudentského spolku Konsent. Celý prostor byl zahalen krásně osvětlenou menzou a vúní výborného jídla od Prague Jerk station 
                nebo veganská alternativa od Soyky. Poté co začal noční klid a pan děkan uklidnil policejní hlídku, tak se celá party přesunula do vnitřních prostorů
                Albertovské menzy, kde se rozjela výborná party s djs až do brzkých ranních hodin.
            </Typography></div>
            <div>
            <Stack direction="row" spacing={2}>
                <Stack direction="column" spacing={1} >
            <div>
                <img src={pic1} style={{maxWidth: '45%'}}></img>
                <img src={pic2} style={{maxWidth: '45%', marginLeft: '10px'}}></img></div>
                <div>
                <img src={pic3} style={{maxWidth: '45%'}}></img>
                <img src={pic4} style={{maxWidth: '45%', marginLeft: '10px'}}></img></div>
                </Stack>
            </Stack></div>
            </Stack>
            </div></div>
            <div id="invisibleWide" style={{marginTop:'50px'}}>
            <Paper sx={{padding: "25px", textAlign:'justify'}}>
                <h2> O spolku přífuk</h2>
                <h4>K čemu Přífuk?</h4>
                <p>Spolek přífuk je studentskou institucí založenou na půdě Přírodovědecké fakulty Univerzity Karlovy. Je je ojedinělý projekt, který má
             za hlavní cíl zlepšovat studentský život v kampusu Albertov. Hlavní myšlenka spolku je prohlubování studentských vztahů a to nejen 
             v rámci sekce, ale i mezisekčně nebo i mezifakultně. Spolek byl, je a vždycky bude otevřen všem proaktivním studentům, kteří se chtějí
             přidat do akademické společnosti a přispět jakoukoliv kvalitou.</p>
             <h4>Jak toho dosáhnout?</h4>
                <p> Spolek není uzavřená společnost, ale je to společenství, které nabízí mnoho příležitostí pro všechny proaktivní studenty.
                    Důležitou myšlenkou spolku je vytvořit instituci, která zastřeší a zjednoduší organizaci jakýchkoliv projektů, se kterými přijde jakýkoliv 
                    člen spolku. Spolek si klade za cíl pomáhat tvořit nové věci a realizovat nápady všech, kteří projeví zájem o zlepšení zdejšího prostředí.
                    Stejně tak zakládá na spolupráci a ochotě pomoct všech všem se vším, s čím kdo přijde. Více v <a href="/Opportunities">Příležitostech</a>
                    </p>
                <h4 style={{textAlign: 'left'}}>Spolek organizuje nejlepší Přírodovědecký festival</h4>
                <p>  K příležitosti (z)bourání Albertovské menzy byl zorganizován oslavný festival právě v prostoru stoleté Albertovské menzy. Několik studentských 
                i nestudentských kapel vystoupilo na pódiu před menzou a vytvořily krásný hudební doprovod celé akce. Mimo to se akce účastnilo mnoho dalších 
                spřátelených studentských spolků, které se zhostily doprovodného programu. Na programu byl nealkoholický pivní pong, spolková prezentace nebo i
                osvěta o sexuálním násilí od nestudentského spolku Konsent. Celý prostor byl zahalen krásně osvětlenou menzou a vúní výborného jídla od Prague Jerk station 
                nebo veganská alternativa od Soyky. Poté co začal noční klid a pan děkan uklidnil policejní hlídku, tak se celá party přesunula do vnitřních prostorů
                Albertovské menzy, kde se rozjela výborná party s djs až do brzkých ranních hodin.
                    </p>
                    <img src={pic1} style={{maxWidth: '100%'}}></img>
                    <img src={pic2} style={{maxWidth: '100%'}}></img>
                    <img src={pic3} style={{maxWidth: '100%'}}></img>
                    <img src={pic4} style={{maxWidth: '100%'}}></img>
            </Paper>




            </div>
    </div> );
}
 
export default About;