import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import {Card, Color} from '@mui/material';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import {Button, ButtonGroup} from '@mui/material';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Welcome from './Welcome'
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Link } from "react-router-dom";
import { deepOrange, lightGreen, pink, orange, purple} from '@mui/material/colors';

const theme = createTheme({
    palette: {
      primary: {
        // Purple and green play nicely together.
        main: purple[500],
      },
      secondary: {
        // This is green.A700 as hex.
        main: '#1e7c1f',
      },
    },
  });

export default function Navbar() { 
  return (
    <Card sx={{width: '75%', marginLeft:'auto', marginRight:'auto', borderRadius: '16px', marginTop:'10px', marginBottom: "15px", zIndex: 5}} ><ThemeProvider theme={theme}>
      <AppBar position="static" color="secondary" >
        <Toolbar sx={{marginLeft: 'auto', marginRight: 'auto', textAlign: 'center'}}>

        <ButtonGroup   variant="text" aria-label="text button group">
          <Button href="/" variant="h6">
            Úvod
          </Button>
          <Button href="/events" variant="h6" sx={{fontFamily: "'Roboto Mono', monospace"}}>
            Akce
          </Button>
          <Button  href="/about" variant="h6">
            O spolku
          </Button>
          <Button  href="/join" variant="h6">
            Přidat se
          </Button>
          <Button  href="/opportunities" variant="h6" >
           Příležitosti
          </Button>
          <Button  href="/members" variant="h6">
            Členové
          </Button>
          <Button  href="/rules" variant="h6">
            Pravidla a stanovy
          </Button></ButtonGroup>
</Toolbar>
      </AppBar>
      </ThemeProvider></Card>
    
  );
}



