import React, { useState, useEffect } from 'react';
import Navbar from './Navbar'
import logo from '../Media/logo.png'
import useFetch3 from '../Requester/useFetch3';
import {Button, ButtonGroup, Card, Input, Typography, Stack} from '@mui/material';

const MembersDashboard = () => {
    const {data3, setData3} = useFetch3();
    const [nameHook, setNameHook] = useState("");
    const [surnameHook, setSurnameHook] = useState("");
    const [sectionHook, setSectionHook] = useState("");
    const [membershipHook, setMembershipHook] = useState("");
    console.log(data3);

    useEffect(() => {
        let m = "https://prifuk.onrender.com/members/api";
              setData3({ ...data3, slug: m });    
    setTimeout(()=>{
        }, 100)
      
      }, [])
    return ( 
       <div>         
    <Navbar></Navbar>
    <Typography sx={{textAlign: 'center', fontSize: '28pt'}} variant="h3">Spolkový rejstřík člennů</Typography>
    <div style={{width:'15%', marginRight:'auto', marginLeft:'auto'}}><Stack direction='row'>
        <Typography>Počet člennů: {data3.results.length}</Typography>
    </Stack></div>
    <div style={{width:'100%', marginRight:'150px', marginLeft:'auto'}} >
    <Card sx={{width: '100%', marginLeft:'145px', marginRight:'auto', borderRadius: '16px', marginTop:'10px', marginBottom: "15px", padding: '10px'}} >
    <Input  placeholder="Jméno" onChange={event => setNameHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    <Input  placeholder="Příjmení" onChange={event => setSurnameHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    <Input  placeholder="Sekce" onChange={event => setSectionHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    <Input  placeholder="Člen" onChange={event => setMembershipHook(event.target.value)} sx={{width:'150px', marginRight:'50px'}}></Input>
    </Card>
    {!data3.results ? "Loading..." : 
data3.results.slice(0).filter(function(q){
    return q.name.includes(nameHook)}).filter(function(a){
        return a.surname.includes(surnameHook)}).filter(function(s){
            return s.section.includes(sectionHook)}).filter(function(d){
                return d.membership.includes(membershipHook)}).map(({ _id, date, email, membership, musicTaste, name, publicationAuth, section, surname, telNumber}) =>
    <div style={{width: '100%', marginLeft:'150px', marginTop:'10px'}} key={date} >
    <Stack direction='row'>
  <Typography sx={{width:'150px', marginRight:'50px'}}>{name}</Typography>
    <Typography sx={{width:'150px', marginRight:'50px'}}>{surname}</Typography>
    <Typography sx={{width:'150px', marginRight:'50px'}}>{section}</Typography>
    <Typography sx={{width:'150px', marginRight:'50px'}}>{membership}</Typography>
    <Typography sx={{width:'150px', marginRight:'100px'}}>{email}</Typography>
    <Typography sx={{width:'150px', marginRight:'50px'}}>{telNumber}</Typography>
    <Typography sx={{width:'150px', marginRight:'50px'}}>{_id}</Typography></Stack>
    </div>
            )}
    
    </div>
    
    </div> );
}
 
export default MembersDashboard;